.input-text {
  display: flex;
  flex-direction: column;
  border: none;
}

.input-text span {
  font-family: inherit;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  padding-left: 16px;
  text-transform: uppercase;
}

.input-text input {
  font-family: inherit;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  flex-grow: 1;
  padding: 16px;
  border: none;
  background-color: $color-input;
}
