.catalog__photo {
  display: block;

  & img {
    margin-bottom: 11px;
  }
}

.catalog__title {
  margin: 0;
  font-weight: 700;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
}

.catalog__author {
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  margin-bottom: 11px;
  color: $gray;
}

// &__item-photo img {
