.catalog__likes {
  display: flex;
  align-items: baseline;

  & a {
    display: block;
    background-image: url("../img/like-icon.svg");
    background-repeat: no-repeat;
    width: 23px;
    height: 21px;
    margin-right: 14px;
  }

  & span {
    font-weight: 700;
    font-size: 14px;
    line-height: 21px;
    color: $black;
  }
}
